"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import {
    Users,
    Plus,
    Search,
    Edit,
    Trash2,
    Eye,
    Filter,
    Download,
    Upload,
    RefreshCw,
    Shield,
    Mail,
    Phone,
    Calendar,
    MapPin,
    MoreHorizontal,
    ChevronLeft,
    ChevronRight,
    ChevronUp,
} from "lucide-react";

interface User {
    id: string;
    username: string;
    email: string;
    ho_ten: string;
    so_dien_thoai: string;
    phong_ban: string;
    chuc_vu: string;
    phan_quyen: string;
    trang_thai: "active" | "inactive";
    ngay_tao: string;
    ngay_cap_nhat: string;
    dia_chi?: string;
    avatar?: string;
}

const NguoiDungManagement = () => {
    const router = useRouter();
    const [users, setUsers] = useState<User[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchQuery, setSearchQuery] = useState("");
    const [selectedUsers, setSelectedUsers] = useState<string[]>([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const [showFilters, setShowFilters] = useState(false);
    const [filters, setFilters] = useState({
        phong_ban: "",
        chuc_vu: "",
        phan_quyen: "",
        trang_thai: "",
    });

    // Mock data - trong thực tế sẽ fetch từ API
    useEffect(() => {
        const mockUsers: User[] = [
            {
                id: "1",
                username: "admin",
                email: "admin@trgcrm.com",
                ho_ten: "Nguyễn Văn Admin",
                so_dien_thoai: "0901234567",
                phong_ban: "Ban Giám đốc",
                chuc_vu: "Giám đốc",
                phan_quyen: "Admin",
                trang_thai: "active",
                ngay_tao: "2024-01-01",
                ngay_cap_nhat: "2024-01-15",
                dia_chi: "Hà Nội",
            },
            {
                id: "2",
                username: "nhanvien1",
                email: "nhanvien1@trgcrm.com",
                ho_ten: "Trần Thị Nhân Viên",
                so_dien_thoai: "0912345678",
                phong_ban: "Kinh doanh",
                chuc_vu: "Nhân viên kinh doanh",
                phan_quyen: "User",
                trang_thai: "active",
                ngay_tao: "2024-01-02",
                ngay_cap_nhat: "2024-01-10",
            },
        ];

        setTimeout(() => {
            setUsers(mockUsers);
            setLoading(false);
        }, 1000);
    }, []);

    // Filter users
    const filteredUsers = users.filter((user) => {
        const matchesSearch =
            user.ho_ten.toLowerCase().includes(searchQuery.toLowerCase()) ||
            user.email.toLowerCase().includes(searchQuery.toLowerCase()) ||
            user.username.toLowerCase().includes(searchQuery.toLowerCase());

        const matchesFilters =
            (!filters.phong_ban || user.phong_ban === filters.phong_ban) &&
            (!filters.chuc_vu || user.chuc_vu === filters.chuc_vu) &&
            (!filters.phan_quyen || user.phan_quyen === filters.phan_quyen) &&
            (!filters.trang_thai || user.trang_thai === filters.trang_thai);

        return matchesSearch && matchesFilters;
    });

    // Handle user selection
    const handleSelectUser = (userId: string) => {
        setSelectedUsers((prev) =>
            prev.includes(userId)
                ? prev.filter((id) => id !== userId)
                : [...prev, userId],
        );
    };

    // Handle select all
    const handleSelectAll = () => {
        if (selectedUsers.length === filteredUsers.length) {
            setSelectedUsers([]);
        } else {
            setSelectedUsers(filteredUsers.map((user) => user.id));
        }
    };

    // Handle delete user
    const handleDeleteUser = (userId: string) => {
        if (window.confirm("Bạn có chắc chắn muốn xóa người dùng này?")) {
            setUsers((prev) => prev.filter((user) => user.id !== userId));
            setSelectedUsers((prev) => prev.filter((id) => id !== userId));
        }
    };

    // Handle delete selected
    const handleDeleteSelected = () => {
        if (
            window.confirm(
                `Bạn có chắc chắn muốn xóa ${selectedUsers.length} người dùng đã chọn?`,
            )
        ) {
            setUsers((prev) =>
                prev.filter((user) => !selectedUsers.includes(user.id)),
            );
            setSelectedUsers([]);
        }
    };

    // Get status badge
    const getStatusBadge = (status: string) => {
        const styles = {
            active: "bg-green-100 text-green-800",
            inactive: "bg-red-100 text-red-800",
        };
        return (
            <span
                className={`px-2 py-1 text-xs font-medium rounded-full ${styles[status as keyof typeof styles]}`}
            >
                {status === "active" ? "Hoạt động" : "Không hoạt động"}
            </span>
        );
    };

    // Get permission badge
    const getPermissionBadge = (permission: string) => {
        const styles = {
            Admin: "bg-purple-100 text-purple-800",
            Manager: "bg-blue-100 text-blue-800",
            User: "bg-gray-100 text-gray-800",
        };
        return (
            <span
                className={`px-2 py-1 text-xs font-medium rounded-full ${styles[permission as keyof typeof styles] || styles.User}`}
            >
                {permission}
            </span>
        );
    };

    return (
        <div className="min-h-screen bg-gray-50 p-6">
            {/* Header */}
            <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
                <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between">
                    <div className="flex items-center mb-4 lg:mb-0">
                        <Users className="w-8 h-8 text-blue-600 mr-3" />
                        <div>
                            <h1 className="text-2xl font-bold text-gray-900">
                                Quản lý người dùng
                            </h1>
                            <p className="text-gray-600">
                                Quản lý thông tin người dùng hệ thống
                            </p>
                        </div>
                    </div>

                    <div className="flex flex-wrap gap-3">
                        <button
                            onClick={() => router.push("/nguoi-dung/tao-moi")}
                            className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition"
                        >
                            <Plus className="w-5 h-5 mr-2" />
                            Thêm người dùng
                        </button>
                        <button className="flex items-center px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition">
                            <Upload className="w-5 h-5 mr-2" />
                            Import
                        </button>
                        <button className="flex items-center px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition">
                            <Download className="w-5 h-5 mr-2" />
                            Export
                        </button>
                        <button
                            onClick={() => window.location.reload()}
                            className="flex items-center px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition"
                        >
                            <RefreshCw className="w-5 h-5 mr-2" />
                            Làm mới
                        </button>
                    </div>
                </div>

                {/* Search and Filters */}
                <div className="mt-6 flex flex-col lg:flex-row gap-4">
                    <div className="flex-1 relative">
                        <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                        <input
                            type="text"
                            placeholder="Tìm kiếm theo tên, email, username..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                        />
                    </div>

                    <button
                        onClick={() => setShowFilters(!showFilters)}
                        className="flex items-center px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition"
                    >
                        <Filter className="w-5 h-5 mr-2" />
                        Bộ lọc
                        {showFilters && <ChevronUp className="w-4 h-4 ml-2" />}
                    </button>
                </div>

                {/* Advanced Filters */}
                {showFilters && (
                    <div className="mt-4 pt-4 border-t border-gray-200">
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                            <select
                                value={filters.phong_ban}
                                onChange={(e) =>
                                    setFilters((prev) => ({
                                        ...prev,
                                        phong_ban: e.target.value,
                                    }))
                                }
                                className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                            >
                                <option value="">Tất cả phòng ban</option>
                                <option value="Ban Giám đốc">
                                    Ban Giám đốc
                                </option>
                                <option value="Kinh doanh">Kinh doanh</option>
                                <option value="Marketing">Marketing</option>
                                <option value="Kế toán">Kế toán</option>
                            </select>

                            <select
                                value={filters.chuc_vu}
                                onChange={(e) =>
                                    setFilters((prev) => ({
                                        ...prev,
                                        chuc_vu: e.target.value,
                                    }))
                                }
                                className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                            >
                                <option value="">Tất cả chức vụ</option>
                                <option value="Giám đốc">Giám đốc</option>
                                <option value="Trưởng phòng">
                                    Trưởng phòng
                                </option>
                                <option value="Nhân viên">Nhân viên</option>
                            </select>

                            <select
                                value={filters.phan_quyen}
                                onChange={(e) =>
                                    setFilters((prev) => ({
                                        ...prev,
                                        phan_quyen: e.target.value,
                                    }))
                                }
                                className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                            >
                                <option value="">Tất cả quyền</option>
                                <option value="Admin">Admin</option>
                                <option value="Manager">Manager</option>
                                <option value="User">User</option>
                            </select>

                            <select
                                value={filters.trang_thai}
                                onChange={(e) =>
                                    setFilters((prev) => ({
                                        ...prev,
                                        trang_thai: e.target.value,
                                    }))
                                }
                                className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                            >
                                <option value="">Tất cả trạng thái</option>
                                <option value="active">Hoạt động</option>
                                <option value="inactive">
                                    Không hoạt động
                                </option>
                            </select>
                        </div>
                    </div>
                )}

                {/* Selected Actions */}
                {selectedUsers.length > 0 && (
                    <div className="mt-4 p-3 bg-blue-50 border border-blue-200 rounded-lg flex items-center justify-between">
                        <span className="text-sm text-blue-800">
                            Đã chọn {selectedUsers.length} người dùng
                        </span>
                        <div className="flex gap-2">
                            <button className="px-3 py-1 text-sm bg-blue-600 text-white rounded hover:bg-blue-700 transition">
                                Gửi email
                            </button>
                            <button
                                onClick={handleDeleteSelected}
                                className="px-3 py-1 text-sm bg-red-600 text-white rounded hover:bg-red-700 transition"
                            >
                                Xóa đã chọn
                            </button>
                        </div>
                    </div>
                )}
            </div>

            {/* Table */}
            <div className="bg-white rounded-lg shadow-sm overflow-hidden">
                {loading ? (
                    <div className="flex items-center justify-center py-12">
                        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
                    </div>
                ) : (
                    <>
                        <div className="overflow-x-auto">
                            <table className="w-full">
                                <thead className="bg-gray-50 border-b border-gray-200">
                                    <tr>
                                        <th className="px-6 py-3 text-left">
                                            <input
                                                type="checkbox"
                                                checked={
                                                    selectedUsers.length ===
                                                    filteredUsers.length
                                                }
                                                onChange={handleSelectAll}
                                                className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                                            />
                                        </th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Thông tin
                                        </th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Liên hệ
                                        </th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Phòng ban/Chức vụ
                                        </th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Quyền
                                        </th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Trạng thái
                                        </th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Ngày tạo
                                        </th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                            Thao tác
                                        </th>
                                    </tr>
                                </thead>
                                <tbody className="bg-white divide-y divide-gray-200">
                                    {filteredUsers.map((user) => (
                                        <tr
                                            key={user.id}
                                            className="hover:bg-gray-50"
                                        >
                                            <td className="px-6 py-4">
                                                <input
                                                    type="checkbox"
                                                    checked={selectedUsers.includes(
                                                        user.id,
                                                    )}
                                                    onChange={() =>
                                                        handleSelectUser(
                                                            user.id,
                                                        )
                                                    }
                                                    className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                                                />
                                            </td>
                                            <td className="px-6 py-4">
                                                <div className="flex items-center">
                                                    <div className="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center mr-3">
                                                        <Users className="w-6 h-6 text-blue-600" />
                                                    </div>
                                                    <div>
                                                        <div className="text-sm font-medium text-gray-900">
                                                            {user.ho_ten}
                                                        </div>
                                                        <div className="text-sm text-gray-500">
                                                            @{user.username}
                                                        </div>
                                                    </div>
                                                </div>
                                            </td>
                                            <td className="px-6 py-4">
                                                <div className="text-sm">
                                                    <div className="flex items-center text-gray-900 mb-1">
                                                        <Mail className="w-4 h-4 mr-1 text-gray-400" />
                                                        {user.email}
                                                    </div>
                                                    <div className="flex items-center text-gray-500">
                                                        <Phone className="w-4 h-4 mr-1 text-gray-400" />
                                                        {user.so_dien_thoai}
                                                    </div>
                                                </div>
                                            </td>
                                            <td className="px-6 py-4">
                                                <div className="text-sm">
                                                    <div className="text-gray-900">
                                                        {user.phong_ban}
                                                    </div>
                                                    <div className="text-gray-500">
                                                        {user.chuc_vu}
                                                    </div>
                                                </div>
                                            </td>
                                            <td className="px-6 py-4">
                                                {getPermissionBadge(
                                                    user.phan_quyen,
                                                )}
                                            </td>
                                            <td className="px-6 py-4">
                                                {getStatusBadge(
                                                    user.trang_thai,
                                                )}
                                            </td>
                                            <td className="px-6 py-4 text-sm text-gray-500">
                                                {new Date(
                                                    user.ngay_tao,
                                                ).toLocaleDateString("vi-VN")}
                                            </td>
                                            <td className="px-6 py-4">
                                                <div className="flex items-center space-x-2">
                                                    <button
                                                        onClick={() =>
                                                            router.push(
                                                                `/nguoi-dung/${user.id}`,
                                                            )
                                                        }
                                                        className="p-1 text-blue-600 hover:text-blue-800 transition"
                                                        title="Xem chi tiết"
                                                    >
                                                        <Eye className="w-4 h-4" />
                                                    </button>
                                                    <button
                                                        onClick={() =>
                                                            router.push(
                                                                `/nguoi-dung/${user.id}/chinh-sua`,
                                                            )
                                                        }
                                                        className="p-1 text-green-600 hover:text-green-800 transition"
                                                        title="Chỉnh sửa"
                                                    >
                                                        <Edit className="w-4 h-4" />
                                                    </button>
                                                    <button
                                                        onClick={() =>
                                                            handleDeleteUser(
                                                                user.id,
                                                            )
                                                        }
                                                        className="p-1 text-red-600 hover:text-red-800 transition"
                                                        title="Xóa"
                                                    >
                                                        <Trash2 className="w-4 h-4" />
                                                    </button>
                                                    <button
                                                        className="p-1 text-gray-600 hover:text-gray-800 transition"
                                                        title="Thêm"
                                                    >
                                                        <MoreHorizontal className="w-4 h-4" />
                                                    </button>
                                                </div>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>

                        {/* Pagination */}
                        <div className="px-6 py-4 border-t border-gray-200 flex items-center justify-between">
                            <div className="text-sm text-gray-700">
                                Hiển thị 1-{filteredUsers.length} của{" "}
                                {filteredUsers.length} kết quả
                            </div>
                            <div className="flex items-center space-x-2">
                                <button className="p-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition disabled:opacity-50">
                                    <ChevronLeft className="w-4 h-4" />
                                </button>
                                <button className="px-3 py-2 bg-blue-600 text-white rounded-lg">
                                    1
                                </button>
                                <button className="p-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition disabled:opacity-50">
                                    <ChevronRight className="w-4 h-4" />
                                </button>
                            </div>
                        </div>
                    </>
                )}
            </div>
        </div>
    );
};

export default NguoiDungManagement;
